"use client";

import { useEffect, useState } from 'react';

type Health = { status: string };
type Hello = { message: string };

const API_BASE = '';

export default function Home() {
  const [health, setHealth] = useState<string>('Loadingâ€¦');
  const [message, setMessage] = useState<string>('(pending)');

  useEffect(() => {
    fetch(`${API_BASE}/api/health`)
      .then((r) => r.json())
      .then((h: Health) => setHealth(h.status))
      .catch(() => setHealth('unreachable'));

    fetch(`${API_BASE}/api/`)
      .then((r) => r.json())
      .then((m: Hello) => setMessage(m.message))
      .catch(() => setMessage('No response'));
  }, []);

  return (
    <main className="min-h-screen p-8">
      <h1 className="text-3xl font-bold">Client Starter</h1>
      <p className="mt-4">Status: <span className="font-mono">{health}</span></p>
      <p className="mt-2">Message from backend: <span className="font-mono">{message}</span></p>
      <p className="mt-6 text-sm opacity-70">Using API base: {API_BASE}</p>
    </main>
  );
}
